<template>
  <div>
    <!-- 预览文章分类 -->
    <el-card class="box-card">
      <div slot="header" class="clearfix header-box">
        <span>文章分类</span>
        <el-button
          style="float: right; padding: 3px 0"
          type="text"
          @click="addCateShowDialogBtnFn"
        >
          添加分类
        </el-button>
      </div>
      <!-- 分类数据的表格 -->
      <el-table :data="cateList" style="width: 100%" border stripe>
        <!-- type是table-column内置属性，他告诉你用index，意思就是第一个单元格用索引值 -->
        <el-table-column
          type="index"
          label="序号"
          width="100"
        ></el-table-column>
        <el-table-column prop="cate_name" label="分类名称"></el-table-column>
        <el-table-column prop="cate_alias" label="分类别名"></el-table-column>
        <el-table-column label="操作">
          <!-- 作用域插槽：template -->
          <!-- scope对象：{row：行对象} -->
          <template v-slot="scope">
            <el-button
              type="primary"
              size="mini"
              @click="updateCateBtnFn(scope.row)"
            >
              修改
            </el-button>
            <el-button
              type="danger"
              size="mini"
              @click="deleteCateBtnFn(scope.row)"
            >
              删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!-- 添加文章分类--对话框
              el-dialog：对话框组件
              title：对话框的标题
              visible：控制对话框是否显示（右侧的Vue变量如果为true显示，false隐藏）
              :visible.sync：组件内检测到对话框关闭（点击蒙层，按esc，按右上角x），它会回传false给右侧的Vue变量
              :before-close：关闭前的回调，会暂停 Dialog 的关闭

          扩展知识点：
              .sync修饰符（类似于v-model）：可以给子组件实现"双向数据绑定"

              本质：给所在标签绑定：props属性名="Vue变量"   ：父组件传变量给子组件
              @update:props属性名=  "val=> Vue变量=val"   ：子组件又把返回的值给父组件
              <标签 :visible.sync="Vue变量"></标签>
              运行时如下：
                  <标签 :visible="Vue变量" @update:visible="val=> Vue变量=val" ></标签>
              子组件传父组件的时候：this.$emit('update:visible',值)

     -->
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      width="30%"
      @close="dialogCloseFn"
    >
      <!-- 添加的表单 -->
      <el-form
        :model="addForm"
        :rules="addRules"
        ref="addRef"
        label-width="80px"
      >
        <el-form-item label="分类名称" prop="cate_name">
          <el-input v-model="addForm.cate_name">{{
            addForm.cate_name
          }}</el-input>
        </el-form-item>
        <el-form-item label="分类别名" prop="cate_alias">
          <el-input v-model="addForm.cate_alias"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancelFn">取 消</el-button>
        <el-button type="primary" @click="confirmFn"> 确 定 </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>

import { getArtCateListAPI, saveArtCateAPI, updateArtCateAPI, delArtCateAPI } from '@/api'
export default {
  name: 'ArtCate',
  data () {
    return {
      cateList: [], // 文章分类数组
      dialogVisible: false, // 增加文章分类对话框--出现/隐藏（false）
      isEdit: false, // 新增状态
      editId: '', // 保存正在要编辑的数据id值
      addForm: { // 添加表单的数据对象
        cate_name: '',
        cate_alias: ''
      },
      addRules: { // 添加表单的验证规则对象
        cate_name: [
          { required: true, message: '请输入分类名称', trigger: 'blur' },
          { pattern: /^\S{1,10}$/, message: '分类名必须是1-10位的非空字符', trigger: 'blur' }
        ],
        cate_alias: [
          { required: true, message: '请输入分类别名', trigger: 'blur' },
          { pattern: /^[a-zA-Z0-9]{1,15}$/, message: '分类别名必须是1-15位的字母数字', trigger: 'blur' }
        ]
      }

    }
  },
  methods: {
    // 获取文章分类列表
    async getArtCateFn () {
      const res = await getArtCateListAPI()
      console.log(res)
      this.cateList = res.data.data
      // console.log(this.addForm)
    },
    // 添加文章分类展示对话框按钮的方法
    addCateShowDialogBtnFn () {
      this.dialogVisible = true
      this.isEdit = false// 变为新增的状态标记
      this.editId = ''// 置空id
    },
    // 对话框确定按钮->点击事件->让对话框消失 和 调用保存文章分类接口
    confirmFn () {
      // 兜底校验
      this.$refs.addRef.validate(async valid => {
        if (valid) {
          // 通过效验
          if (!this.isEdit) {
            // 新增
            const { data: res } = await saveArtCateAPI(this.addForm)
            console.log(res)
            if (res.code !== 0) return this.$message.error('更新用户信息失败')
            this.$message.success('更新成功')
          } else {
            // 修改
            // this.addForm.id = this.editId// 把要编辑的文章id添加到对象上
            // updateArtCateAPI(this.addForm )
            const { data: res } = await updateArtCateAPI({ id: this.editId, ...this.addForm })
            console.log(res)
            if (res.code !== 0) return this.$message.error('更新用户信息失败')
            this.$message.success('更新成功')
          }

          // 重新请求一次文章列表，拿到最新数据，让表格更新
          this.getArtCateFn()
          this.dialogVisible = false
        } else {
          return false
        }
      })
    },
    // 对话框取消按钮
    cancelFn () {
      this.dialogVisible = false
    },
    // 对话框关闭时的回调
    dialogCloseFn () {
      // el-form提供了一个重置表单(并且还能重置报错提示)
      this.$refs.addRef.resetFields()
    },
    // 修改分类按钮->点击事件（先做数据回调）
    updateCateBtnFn (obj) {
      // obj的值就是（id，cate_name,cate_alias）
      console.log(obj)
      this.isEdit = true
      this.editId = obj.id
      this.dialogVisible = true

      // 等展示真实DOM后，在给数据回显
      this.$nextTick(() => {
        this.addForm.cate_name = obj.cate_name
        this.addForm.cate_alias = obj.cate_alias
      })
    },
    // 删除分类按钮->点击事件
    async deleteCateBtnFn (obj) {
      const { data: res } = await delArtCateAPI(obj.id)
      console.log(res)
      if (res.code !== 0) return this.$message.error('删除用户信息失败')
      this.$message.success('删除成功')
      // 删除后再调用方法去获取最新数据来更新页面
      this.getArtCateFn()
    }
  },
  created () {
    // 获取文章分类列表
    this.getArtCateFn()
  }
}
</script>

<style lang="less" scoped>
.header-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
</style>
